import { React, useEffect, useState } from "react";
import { getReciters, getSowar } from "../hooks/Api";
import ReactAudioPlayer from "react-audio-player";
import Select from "react-select";

const Search = () => {
  const [reciters, setReciters] = useState([]);
  const [currentReciter, setCurrentReciter] = useState({});
  const [souras, setSouras] = useState([]);
  const [currentSoura, setCurrentSoura] = useState();
  const [requestError, setRequestError] = useState(false);

  useEffect(() => {
    async function fetchReciters() {
      console.log("fetching Reciters...");
      let response = await getReciters();
      response.errors ? setRequestError(true) : setReciters(response);
    }
    fetchReciters();
  }, []);

  useEffect(() => {
    async function fetchSowar() {
      console.log(`fetching Sowar ...`);
      let response = await getSowar();
      response.errors ? setRequestError(true) : setSouras(response);
    }
    fetchSowar();
  }, []);

  useEffect(() => {
    console.log(currentReciter);
    console.log(currentSoura);
  }, [currentSoura, currentReciter]);

  return (
    <div className="main-container">
      {requestError && <span>requestError</span>}
      <h2>Quran Listner V1</h2>
      <Select
        name="reciters"
        className="basic-single"
        classNamePrefix="select"
        //defaultValue={colourOptions[0]}
        //isDisabled={isDisabled}
        //isLoading={isLoading}
        isClearable={true}
        isRtl={true}
        isSearchable={true}
        onChange={(e) => {
          console.log(e);
          setCurrentReciter({ ...JSON.parse(e.value) });
          setCurrentSoura(JSON.parse(e.value).suras.split(","));
        }}
        options={reciters.map((reciter) => ({
          label: reciter.name,
          value: JSON.stringify({
            name: reciter.name,
            server: reciter.Server,
            rewaya: reciter.rewaya,
            count: reciter.count,
            letter: reciter.letter,
            suras: reciter.suras
          })
        }))}
      />

      <Select
        name="suras"
        className="basic-single"
        classNamePrefix="select"
        //defaultValue={colourOptions[0]}
        //isDisabled={isDisabled}
        //isLoading={isLoading}
        isClearable={true}
        isRtl={true}
        isSearchable={true}
        options={souras.map((s) => ({ label: s.name, value: s.id }))}
      />
      {/*
      <select
        name="reciters_list"
        onChange={(e) => {
          console.log(e.target.value);
          setCurrentReciter({ ...JSON.parse(e.target.value) });
          setCurrentSoura(JSON.parse(e.target.value).suras.split(","));
        }}
      >
        <option>Select...</option>
        {reciters.map((reciter) => (
          <option
            key={reciter.id}
            value={JSON.stringify({
              name: reciter.name,
              server: reciter.Server,
              rewaya: reciter.rewaya,
              count: reciter.count,
              letter: reciter.letter,
              suras: reciter.suras
            })}
          >
            {reciter.name}
          </option>
        ))}
      </select>
      
      <select name="sowars_list">
        {souras.map((soura) => (
          <option id={soura.id} key={soura.id} value={soura.id}>
            {soura.name}
          </option>
        ))}
      </select>
      */}
      <div className="list-container">
        {currentSoura &&
          currentSoura.map((soura) => {
            let souraFile = soura.padStart(3, "0");
            let url = `${currentReciter.server}/${souraFile}.mp3`;
            let souraName = souras.find((s) => s.id === soura).name;
            return (
              <div key={soura} className="player">
                <h4>
                  {souraName} {soura} - {currentReciter.name} -{" "}
                  {currentReciter.rewaya}
                </h4>
                <ReactAudioPlayer
                  src={url}
                  //autoPlay
                  controls
                />
              </div>
            );
          })}
      </div>
    </div>
  );
};

export default Search;
