import axios from "axios";

const api = axios.create({
  baseURL: "https://mp3quran.net/api/",
  headers: { "Content-Type": "application/json" }
});

export const getReciters = () => {
  return api
    .get("/_arabic.json")
    .then((response) => {
      return response.data.reciters;
    })
    .catch((error) => {
      return error;
    });
};

export const getSowar = (reciter) => {
  return api
    .get("_arabic_sura.json")
    .then((response) => {
      return response.data.Suras_Name;
    })
    .catch((error) => {
      return error;
    });
};
